@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background: linear-gradient(to top left, #373b44, #73c8a9);
}

.expense-date {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    border: 2px solid #557174;
    border-radius: 12px;
    background-color: #557174;
    width: 5.7rem;
    height: 6rem;
    margin: 0 10px;
}

.expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
    letter-spacing: 1.3px;
}

.expense-date__year {
    font-size: 0.75rem;
    padding: 0.2rem 0;
    letter-spacing: 1.3px;
}

.expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: 1.3px;
}
.card {
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(200px);
          backdrop-filter: blur(200px);
  -webkit-filter: blur();
          filter: blur();
  box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.25);
}

.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem auto;
    padding: 0.5rem;
    border-radius: 12px;
    background-color: #87A8A4;
    color: #fff;
}

.expense-item__description {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    flex: 1 1;
}


.expense-item h2 {
    margin: 0 0.7rem;
    font-size: 1.4rem;
    flex: 1 1;
}

.expense-item__price {
    font-size: 1.3rem;
    font-weight: bold;
    background-color:#557174;
    padding: 0.5rem 1.5rem;
    border: 1px solid #557174;
    border-radius: 12px;
}

@media (max-width: 580px) {
    .expense-item__description {
        flex-direction: column;
    }
}
.expenses-list {
    list-style: none;
    padding: 0;
  }
  
  .expenses-list__fallback {
    color: white;
    text-align: center;
  }
.expenses-filter {
    color: white;
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid #fff;
    border-radius: 12px;
    background-color: #fff;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  
  .chart-bar__fill {
    background-color: #FBC6A4;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }

.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #557174;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
.expenses {
    width: 50rem;
    max-width: 95%;
    margin: 2rem auto;
    padding: 1rem;
    border-radius: 12px;
}
.expense-form {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    text-align: left;
    margin-bottom: 1rem;
}

.expense-form__control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.expense-form__control input {
   font: inherit;
   padding: 0.5rem;
   border: 1px solid #ccc;
   border-radius: 12px;
   width: 20rem;
   max-width: 100%;
}

.expense-form__actions {
   text-align: right;
}
 

.expense-form__actions button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #557174;
    background-color: #557174;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
    align-items: right;
  }


.expense-form__actions button:hover, .expense-form__actions button:active {
    background-color: #557174;
    border-color: #557174;
}

.expense-form__action button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .expense-form__action button.alternative:hover,
  .expense-form__action button.alternative:active {
    background-color: #557174;
  }
.new-expense {
  width: 50rem;
  max-width: 95%;
  margin: 2rem auto;
  padding: 1rem;
  text-align: center;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(200px);
          backdrop-filter: blur(200px);
  -webkit-filter: blur();
          filter: blur();
  box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #557174;
  background-color: #557174;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

